.offers {
  width: 100%;
  min-height: 532px;
  box-sizing: border-box;
  flex-direction: column;
  align-items: center;
  display: flex;
}

.offers__title {
  color: #000;
  text-align: center;
  text-shadow: 1px 0 10px #000;
  font-family: Gelasio;
  font-size: 43px;
  font-style: normal;
  font-weight: 500;
  line-height: 60px;
}

.offers__subtitle {
  text-align: center;
  color: #000;
  opacity: .6;
  text-align: center;
  margin-top: 20px;
  font-family: Roboto;
  font-size: 18px;
  font-style: normal;
  font-weight: 400;
  line-height: 29px;
}

.offers__content {
  max-width: 1600px;
  justify-content: space-between;
  align-items: center;
  padding: 60px 0;
  display: flex;
}

.offers__content__block {
  width: 262px;
  cursor: pointer;
  box-sizing: border-box;
  flex-direction: column;
  margin: 0 15px;
  padding: 0 5px;
  transition: all .5s;
  display: flex;
  box-shadow: 0 6px 35px #00000014;
}

.offers__content__block__image img {
  width: 100%;
}

.offers__content__block__title {
  color: #000;
  font-family: Gelasio;
  font-size: 24px;
  font-style: normal;
  font-weight: 500;
  line-height: 36px;
}

.offers__content__block__description {
  justify-content: space-between;
  display: flex;
}

.offers__content__block__description__price p:first-child {
  color: #000;
  opacity: .6;
  font-family: Roboto;
  font-size: 20px;
  font-style: normal;
  font-weight: 400;
  line-height: 30px;
}

.offers__content__block__description__price p:nth-child(2) {
  color: #000;
  opacity: .6;
  font-family: Roboto;
  font-size: 16px;
  font-style: normal;
  font-weight: 400;
  line-height: 24px;
}

.offers__content__block__description__price p:nth-child(3) {
  color: #000;
  font-family: Roboto;
  font-size: 18px;
  font-style: normal;
  font-weight: 500;
  line-height: 27px;
}

.offers__content__block__description__price p:nth-child(3):after {
  content: "/person";
  color: #000;
  opacity: .6;
  font-family: Roboto;
  font-size: 18px;
  font-style: normal;
  font-weight: 500;
  line-height: 27px;
}

.offers__content__block__description__rating {
  color: #000;
  font-family: Roboto;
  font-size: 18px;
  font-style: normal;
  font-weight: 400;
  line-height: 27px;
}

.offers__content__block:hover {
  transform: scale(1.2);
}

.offers__scroll {
  text-align: center;
  align-items: center;
  padding-bottom: 143px;
  display: flex;
}

.offers__scroll__lines {
  margin: 0 40px;
  display: flex;
}

.offers__scroll__line {
  width: 75px;
  height: 5px;
  background: #c9453b1a;
  border-radius: 10px;
  margin: 0 10px;
}

.offers__scroll__line2 {
  width: 75px;
  height: 5px;
  background: #c9453b;
  border-radius: 10px;
}

.offers__scroll__vector {
  opacity: .3;
  cursor: pointer;
}

@media screen and (max-width: 1520px) {
  .offers__content {
    flex-wrap: wrap;
    justify-content: center;
  }

  .offers__content__block {
    padding-top: 20px;
  }

  .offers__scroll {
    display: none;
  }
}

@media screen and (max-width: 1024px) {
  .offers__content {
    flex-wrap: wrap;
    justify-content: center;
  }

  .offers__content__block {
    margin-top: 20px;
  }

  .offers__content__block:nth-child(2n) {
    display: none;
  }
}

